import React from "react";
import { data } from "../utils/data";

export const NetworkTab =()=>{    
    return (
        <div className="">
                <select className="focus:outline-none p-0">
                    {
                        data.map((item)=>{
                           return (<option> <img  src={item.icon} /> {item.name}</option>)
                        })
                    }       
                </select>      
        </div>
    )
}